import { JsonML, getChildren, unitsConverter, normalizeJsonMLChildren } from '@ali/4ever-utils';
import { toStyleObject } from '@ali/4ever-cangjie';
import msWordParseSymbol from './utils/msWordParseSymbol';
import type { MoAttributes as Attributes, HTMLToJsonMLRule, MoState as State } from '@ali/4ever-cangjie';
import {
  getFlattenedChildren,
  isMsWordList,
  removeSymbolText,
  shouldFlattenChildren,
  transfromIndLeft,
} from './utils';

const MSO_LIST = 'msoList';

function parseFromMsWord(): HTMLToJsonMLRule {
  let listData;
  const msoListMap = {};
  const temp = {
    // 是否在列表中
    wordList: false,

    // 是否是开始位置
    fromStart: true,

    // 上一个新增text内容
    preTxt: '',

    preStyle: '',

    // 上一个列表level
    lastLevel: 0,
    isOrdered: null as any,
  };
  return {
    name: 'list',
    onOpenTag: (state: State, name: string, attr: Attributes): boolean => {
      let inlineStyle;
      if (name === 'ol' || name === 'ul') {
        temp.isOrdered = name === 'ol';
      }
      if (isMsWordList(temp.preStyle) && attr.style?.includes('mso')) {
        inlineStyle = toStyleObject(attr.style);
        if (inlineStyle.fontSize) {
          listData.list.symbolStyle = {
            ...listData.list.symbolStyle,
            sz: parseInt(inlineStyle.fontSize),
          };
        }
        if (inlineStyle.color) {
          listData.list.symbolStyle = {
            ...listData.list.symbolStyle,
            color: inlineStyle.color,
          };
        }
        temp.preStyle = '';
      }
      if (!isMsWordList(attr?.style)) {
        return false;
      }
      temp.preStyle = attr.style;
      inlineStyle = inlineStyle || toStyleObject(attr.style);
      if (!inlineStyle[MSO_LIST]) {
        return false;
      }
      state.wrapList('ordered-list');
      temp.wordList = true;
      const mso = inlineStyle[MSO_LIST].split(' ');
      const level = Math.max(parseInt(mso[1].slice(5)) - 1, 0);
      const listId = mso[2];
      const type = name.includes('h') ? name : 'p';
      let indLeft = transfromIndLeft(inlineStyle.marginLeft || '', level);

      if (inlineStyle.textIndent) {
        const textIndent = unitsConverter.toPX(inlineStyle.textIndent);
        if (textIndent > 0) {
          indLeft += textIndent;
        }
      }

      state.currentListId = listId;
      state.currentListLevel = level;
      if (!msoListMap[listId]) {
        msoListMap[listId] = {};
      }
      if (msoListMap[listId][level] === undefined || level > temp.lastLevel) {
        msoListMap[listId][level] = 1;
      } else {
        msoListMap[listId][level]++;
      }
      listData = {
        list: {
          listId,
          isOrdered: state.isParentOrderedList(),
          level,
        },
        ind: {
          left: indLeft,
        },
        jc: inlineStyle.textAlign || 'left',
      };
      if (name.includes('h')) {
        listData.list.symbolStyle = {
          bold: true,
        };
      }
      const li: JsonML = [type, listData] as JsonML;
      state.push(li);
      return true;
    },

    onText: (state: State, txt: string) => {
      if (!temp.wordList) return false;
      if (temp.fromStart && txt.trim() !== '') {
        // 通过解析text内容，获取列表类型，更新列表信息
        temp.fromStart = false;
        const listId = state.currentListId;
        const level = state.currentListLevel;
        if (!listId || level === undefined || level === null) return false;
        const [format, text, type] = msWordParseSymbol(
          txt,
          msoListMap[listId][level],
          level,
          temp.isOrdered,
        );
        if (format !== 'bullet') {
          state.unwrapList();
          state.wrapList('ordered-list');
        }
        listData.list.isOrdered = format !== 'bullet';
        listData.list.listStyleType = type;
        listData.list.listStyle = {
          ...listData.list.listStyle,
          format,
          text,
        };
      }
      if (
        temp.preTxt.trim() !== '' &&
        !(!listData.list.isOrdered && temp.preTxt.length !== 1)
      ) {
        // 删除项目符号相关text，仅保留列表内容
        removeSymbolText(state, temp.preTxt);
      }
      temp.preTxt += txt;
      return false;
    },

    onCloseTag: (state: State, name: string, attr: Attributes): boolean => {
      if (name === 'ol' || name === 'ul') {
        temp.isOrdered = null;
      }
      if (!isMsWordList(attr?.style)) {
        return false;
      }
      temp.wordList = false;
      temp.fromStart = true;
      state.unwrapList();
      const li = state.pop();
      if (shouldFlattenChildren(li)) {
        const flattenedChildren: JsonML[] = getFlattenedChildren(li);
        state.append(...flattenedChildren);
      } else if (getChildren(li).length) {
        state.append(normalizeJsonMLChildren(li));
      }
      temp.preTxt = '';
      return true;
    },
  };
}

export default parseFromMsWord;
